import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)
export default new Vuex.Store({
	//当做data
	state: {
		userRole: null, // 用于存储用户角色
	},
	mutations: {
		setUserRole(state, role) {
			state.userRole = role;
		},
	},
	actions: {
		updateUserRole({ commit }, role) {
			commit('setUserRole', role);
		},
	},
})
